ized fabric cutting tables, RF and Impulse fabric welding machines, and inspection hoists that enable Polar Shades to manufacture the Best Built, Best Backed line of interior and exterior shades in the nation. Polar Shades now supplies well over 1,000 independent dealers nationwide, sharing their exclusive products. Steve even started a second company called All Seasons Retractable Screens, a retractable screen door company (now run by Art Mevius) that manufactures and supplies a nationwide dealer network.\n    ")]), _vm._v(" "), _c('p', [_vm._v("\n      Since its inception, Polar Shades Sun Control has added exterior retractable shades, interior retractable shades, and an electrical division to its product line. The motto for the company, as Steve has always said, is Quality you can trust with service you can count on! With that basic phrase he has positioned Polar Shades to be one of the nations leaders in Sun Control Products. Honesty and integrity is what our business is based on.\n    ")]), _vm._v(" "), _c('p', [_vm._v("\n      Whatever your needs are, large or small, Polar Shades Sun Control should be your choice for Covering everything under the sun. Please give us a call and our friendly staff will be happy to help you with whatever you need. Come visit our showroom which is open to the public 6 days a week, and see for yourself the Best Built, Best Backed shades in the industry.\n    ")])])
